body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }

.btns {
  width: 100%;
}

.btn {
  background-color: #302f2a;
  color: white;
  padding: 15px 32px;
  text-align: center;
  border-style: solid;
  border-color: #585959;
  width: calc(25% - 10px);
  margin: 5px;
  display: inline-block;
  box-sizing: border-box;
}
.btn:hover {
  background-color: #20201c;
  cursor: pointer;
}

.display {
  background-color: grey;
  width: 100%;
  box-sizing: border-box;
  color: white;
  padding: 5px 10px;
  font-size: 24px;
  text-align: right;
  display: inline-block;
  border-style: solid;
}

#calculator {
  background-color: honeydew;
  border: black;
  width: 66%;
}